.footer {
  background: #2F303A;
  padding: 60px 0;
  &__inner {
    display: flex;
    align-items: baseline;
  }
  &__contacts {
    display: block;
    max-width: 100%;
    text-align: left;
  }
  &__email {
    margin-bottom: 9px;
  }
  &__email,
  &__tel {
    display: block;
    color: rgba(255, 255, 255, 0.6);
    line-height: 24px;
    font-size: $fs-14;
    &:hover,
    &:hover {
      color: $accent;
    }
  }
  &__address {
    font-style: normal;
    line-height: 24px;
    font-size: $fs-14;
    color: #ffffff;
    margin-bottom: 9px;
  }
  &__socials {
    display: block;
    max-width: 100%;
    margin-left: 90px;
    margin-right: 98px;
  }
  &__socials-title {
    font-size: $fs-14;
    font-weight: bold;
    text-transform: uppercase;
    color: #fff;
    margin-bottom: 20px;
  }
  &__link_svg-color_white path {
    fill: #fff;
  }
  &__link_bg_grey {
    background: rgba(255, 255, 255, 0.1);
  }
  &__mailing {
    flex-grow: 1;
  }
  &__mailing-title {
    margin-bottom: 20px;
    font-size: $fs-14;
    font-weight: bold;
    text-transform: uppercase;
    color: #fff;
    letter-spacing: normal;
  }
  &__mailing-input {
    background-color: #2F303A;
    max-width: 358px;
    width: 100%;
    padding: 16px;
    border: 1px solid $second-text;
    border-radius: 4px;
    margin-right: 12px;
    color: #fff;
    &:focus {
      box-shadow: 0px 0px 1px 0.5px $accent;
    }
  }
  &__mailing-btn {
    display: inline-block;
    padding: 10px 31px;
  }
  &__mailing-svg {
    position: relative;
    display: inline-block;
    line-height: 30px;
    padding-right: 28px;
    &::after {
      content: '';
      display: block;
      position: absolute;
      background: url(../img/footer/plane.png) no-repeat center / cover;
      width: 24px;
      height: 24px;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
    }
  }
