.modal {
  max-width: 528px;
  width: 100%;
  max-height: 581px;
  background: #fff;
  position: fixed;
  opacity: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  padding: 40px;
  visibility: hidden;
  border-radius: 4px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  &__general-styles {
    max-width: 448px;
    width: 100%;
    border: 1px solid rgba(33, 33, 33, 0.2);
    border-radius: 4px;
  }
  &__input-general {
    transition: all .3s ease;
    padding: 11px 0 11px 42px;
    margin-bottom: 10px;
  }
  &__close-btn {
    border: 1px solid rgba(0, 0, 0, 0.1);
    background: none;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    padding: 6px;
    path {
      transition: all 0.3s ease;
    }
    &:hover path {
      fill: $accent;
    }
  }
  &__close-wrapper {
    position: absolute;
    width: 18px;
    height: 18px;
    top: 8px;
    right: 14px;
  }
  &__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    letter-spacing: 0.03em;
    color: $second-color;
    margin-bottom: 12px;
  }
  &__label {
    display: block;
    position: relative;
  }
  &__label-name {
    display: block;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.01em;
    color: $second-text;
    margin-bottom: 4px;
  }
  &__svg {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(16px, 32px);
    path {
      transition: all 0.3s ease;
    }
  }
  &__comment-area {
    display: block;
    resize: none;
    height: 120px;
    padding: 12px 16px;
    margin-bottom: 20px;
    &::placeholder {
      color: $second-text;
    }
  }
  &__agree-wrapper {
    display: block;
    text-align: center;
    margin-bottom: 30px;
  }
  &__agree-checkbox {
    display: inline-block;
    position: relative;
    top: 1px;
    width: 16px;
    margin-right: 8px;
  }
  &__agree-mailing {
    font-weight: 400;
    font-size: $fs-14;
    line-height: 24px;
    letter-spacing: $ls-3;
    color: $second-text;
  }
  &__agree-contract {
    font-weight: 400;
    font-size: $fs-14;
    line-height: 24px;
    letter-spacing: $ls-3;
    text-decoration-line: underline;
    color: $accent;
  }
  &__send-btn {
    display: block;
    padding: 10px 55px;
    margin: 0 auto;
    &:hover {
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
  }
}

.focus_color:focus {
  border-color: $accent;
}

.focus_color:focus+.modal__svg path {
  fill: $accent;
}

.focus_color:focus+.modal__svg path {
  transition: all .3s ease;
}
