$fs-16: 16px;
$mobile-breakpoint: 481px;
$tablet-breakpoint: 769px;

@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    font-family: "#{$font-family}";
    src: url('../fonts/#{$url}.woff2') format("woff2"),
    url('../fonts/#{$url}.woff') format("woff");
    font-weight: #{$weight};
    font-display: swap;
    font-style: $style;
  }
}


.hamburger {
  padding: 20.5px 15px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
}

.hamburger:hover .hamburger-inner,
.hamburger:hover .hamburger-inner::before,
.hamburger:hover .hamburger-inner::after {
  background: $accent;
}

.hamburger.is-active:hover .hamburger-inner,
.hamburger.is-active:hover .hamburger-inner::before,
.hamburger.is-active:hover .hamburger-inner::after {
  background: $accent;
}

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: $second-color;
}

.hamburger-box {
  width: 24px;
  height: 16px;
  display: inline-block;
  position: relative;
}

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px;
}

.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  width: 24px;
  height: 3px;
  background-color: $second-color;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.hamburger-inner::before,
.hamburger-inner::after {
  content: "";
  display: block;
}

.hamburger-inner::before {
  top: -7px;
}

.hamburger-inner::after {
  bottom: -7px;
}


.hamburger--squeeze .hamburger-inner {
  transition-duration: 0.075s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger--squeeze .hamburger-inner::before {
  transition: top 0.075s 0.12s ease, opacity 0.075s ease;
}

.hamburger--squeeze .hamburger-inner::after {
  transition: bottom 0.075s 0.12s ease, transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger--squeeze.is-active .hamburger-inner {
  transform: rotate(45deg);
  transition-delay: 0.12s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}

.hamburger--squeeze.is-active .hamburger-inner::before {
  top: 0;
  opacity: 1;
  transition: top 0.075s ease, opacity 0.075s 0.12s ease;
}

.hamburger--squeeze.is-active .hamburger-inner::after {
  bottom: 0;
  transform: rotate(-90deg);
  transition: bottom 0.075s ease, transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
}

body {
  font-family: 'Roboto', sans-serif;
}

button,
input,
textarea {
  font-family: 'Roboto', sans-serif;
}

.container {
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  position: relative;
}

.banner {
  position: relative;
  background: url(../img/main-page/banner.png) no-repeat center / cover;
  text-align: center;
  padding: 200px 0;
  &::before {
    content: "";
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(47, 48, 58, 0.4);
  }
  &__title {
    display: block;
    font-size: 44px;
    color: #ffffff;
    line-height: 60px;
    font-weight: 900;
    letter-spacing: $ls-6;
    text-align: center;
    text-transform: uppercase;
    max-width: 696px;
    margin: 0 auto;
    margin-bottom: 30px;
  }
  &__order-btn {
    padding: 10px 39px;
  }
}

.company-pluses {
  padding: 94px 0 47px;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item {
    flex-basis: calc(25% - 30px);
  }
  &__img-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    background: #F5F4FA;
    margin-bottom: 30px;
  }
  &__img {
    padding: 25px 0;
  }
  &__title {
    display: block;
    font-size: $fs-14;
    font-weight: bold;
    letter-spacing: $ls-3;
    text-transform: uppercase;
    color: $second-color;
    margin-bottom: 10px;
  }
  &__description {
    font-size: $fs-14;
    line-height: 24px;
    color: $second-text;
    letter-spacing: $ls-3;
  }
}

.what-we-do {
  padding: 47px 0 94px;
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__item {
    position: relative;
    flex-basis: calc(33.3333% - 30px);
  }
  &__image {
    max-width: 100%;
    display: block;
    width: 100%;
  }
  &__image-description {
    position: absolute;
    text-align: center;
    width: 100%;
    font-size: $fs-14;
    letter-spacing: $ls-3;
    text-transform: uppercase;
    font-weight: bold;
    color: #fff;
    padding: 27px 10px;
    bottom: 0;
    left: 0;
    background-color: rgba(44, 50, 53, 0.8);
  }
}

.our-team {
  background-color: #F5F4FA;
  padding: 94px 0;
  &__social {
    display: flex;
    justify-content: space-between;
  }
  &__social-card {
    flex-basis: calc(25% - 20px);
    box-shadow: 0px 2px 2px grey;
    text-align: center;
    background: #fff;
  }
  &__social-photo {
    max-width: 100%;
    width: 100%;
    display: block;
  }
  &__name {
    display: block;
    font-size: $fs-16;
    line-height: 18.75px;
    letter-spacing: $ls-3;
    font-weight: 500;
    color: $second-color;
    margin-bottom: 10px;
  }
  &__name-item {
    background: #fff;
    padding: 30px 0 8px;
  }
  &__profession {
    display: block;
    font-size: $fs-16;
    font-weight: 400;
    line-height: 18.75px;
    letter-spacing: $ls-3;
    color: $second-text;
  }
  &__socials {
    padding: 8px 32px 30px 32px;
  }
}

.clients {
  padding: 94px 0;
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item {
    flex-basis: calc(16.666% - 30px);
    border: 1px solid #AFB1B8;
    border-radius: 4px;
    &:hover,
    &:hover path {
      fill: $accent;
      border-color: $accent;
    }
  }
  &__item-logo {
    display: flex;
    height: 92px;

    svg {
      margin: auto;
    }
  }
}

.socials {
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 44px;
    height: 44px;
    border-radius: 50%;
    &:hover path,
    &:hover {
      fill: #ffffff;
      background: $accent;
    }
  }
  &__link path,
  &__link {
    transition: all .3s ease;
  }
}

.mobile-menu {
  position: absolute;
  z-index: 1000;
  top: 0;
  right: 0;
  width: 100%;
  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    max-width: 480px;
    padding: 48px 84px 48px 40px;
    transform: translateX(100%);
    transition: all .3s ease;
  }
  &__button {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1001;
  }
  &__socials-wrapper {
    display: flex;
  }
  &__link {
    display: block;
    font-size: 34px;
    line-height: 47px;
    letter-spacing: 0.02em;
    color: $second-color;
    &:hover {
      color: $accent;
    }
  }
  &__tel {
    display: block;
    font-size: 28px;
    line-height: 40px;
    letter-spacing: 0.02em;
    color: $accent;
    &:hover {
      color: #1b7ac7;
    }
  }
  &__email {
    display: block;
    font-size: 18px;
    line-height: 28px;
    letter-spacing: 0.02em;
    color: $second-text;
    margin-bottom: 64px;
    &:hover {
      color: $accent;
    }
  }
  &__social-link {
    display: block;
    line-height: 22px;
    letter-spacing: 0.02em;
    color: $accent;
    margin: 0 10px;
    &:hover {
      color: #1b7ac7;
    }
  }
  &__social:not(:last-child) {
    border-right: 1px solid rgba(33, 33, 33, 0.2);
  }
}

.ml-0 {
margin-left: 0;
}

.mr-0 {
  margin-right: 0;
}

.mb-32 {
  margin-bottom: 32px;
}

.general-btn-style {
  font-size: $fs-16;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: $ls-6;
  background: $accent;
  color: #ffffff;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.general-btn-style:hover {
  background: #1b7ac7;
}

.section__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 42.19px;
  text-align: center;
  letter-spacing: 0.03em;
  color: $second-color;
  margin-bottom: 50px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  pointer-events: none;
  transition: all .3s ease;
  z-index: 999;
} 

.active-modal {
  opacity: 1;
  pointer-events: all;
  z-index: 99999;
  visibility: visible;
  transition: all .3s ease;
}

.active-mobile {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
  transition: all .3s ease;
}

.no-scroll {
  overflow: hidden !important;
}

$accent: #2196F3;
$second-color: #212121;
$second-text: #757575;
$ls-3: 0.03em;
$ls-6: 0.06em;
$fs-14: 14px;
$fs-16: 16px;
$mobile-breakpoint: 481px;
$tablet-breakpoint: 769px;

@mixin font-face($font-family, $url, $weight, $style) {
  @font-face {
    font-family: "#{$font-family}";
    src: url('../fonts/#{$url}.woff2') format("woff2"),
    url('../fonts/#{$url}.woff') format("woff");
    font-weight: #{$weight};
    font-display: swap;
    font-style: $style;
  }
}


@media (min-width: $mobile-breakpoint) {
  .mobile-menu {
    display: none;
  }
}

@media (min-width: $tablet-breakpoint) {
  .container {
    max-width: 753px;
  }

}

@media (min-width: 1022px) {
  .container {
    max-width: 1000px;
  }
}

@media (min-width: 1230px) {
  .container {
    max-width: 1200px;
  }
}


