@media (max-width: 768px) {
  .logo {
    display: initial;
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 0.03em;
  }
  .header__contact {
    display: block;
  }
  .header__contact-email,
  .header__contact-tel {
    font-size: 12px;
    line-height: 14px;
    padding: 0;
  }
  .header__contact-email {
    margin-bottom: 10px;
  }
  .header__nav-link {
    display: inline-block;
  }
  .header__nav {
    margin-left: 86px;
    margin-right: 84px;
  }
  .banner {
    padding: 100px 0;
  }
  .banner__title {
    max-width: 360px;
    font-size: 26px;
    line-height: 42px;
    letter-spacing: 0.06em;
  }
  .banner__order-btn {
    padding: 10px 32px;
  }
  .company-pluses {
    padding: 60px 0;
  }
  .company-pluses__inner {
    flex-wrap: wrap;
    align-items: stretch;
  }
  .company-pluses__item {
    flex-basis: calc(50% - 30px);
    &:nth-child(-n+2) {
      margin-bottom: 30px;
    }
  }
  .what-we-do {
    display: none;
  }
  .section__title {
    font-size: 28px;
    line-height: 33px;
  }
  .our-team {
    padding: 60px 0;
  }
  .our-team__social {
    flex-wrap: wrap;
  }
  .our-team__social-card {
    flex-basis: calc(50% - 15px);
    &:nth-child(-n+2) {
      margin-bottom: 30px;
    }
  }
  .socials {
    justify-content: center;
  }
  .socials__link:not(:last-of-type) {
    margin-right: 10px;
  }
  .clients {
    padding: 60px 0;
  }
  .clients__inner {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
  }
  .footer__inner {
    flex-wrap: wrap;
    justify-content: center;
  }
  .footer__contacts {
    text-align: center;
    margin-bottom: 60px;
  }
  .footer__socials {
    text-align: center;
    margin-left: 165px;
    margin-right: 0;
  }
  .footer__mailing {
    text-align: center;
    margin: auto;
  }
  .footer__mailing-input {
    max-width: 450px;
    margin-bottom: 20px;
    margin-right: 0;
  }
  .footer__mailing-btn {
    display: block;
    padding: 10px 28px;
    margin: 0 auto;
  }
  .categories {
    padding: 60px 0;
  }
  .categories__navigation {
    margin-bottom: 30px;
  }
  .categories__inner {
    display: grid;
    grid-template-columns: repeat(2,1fr);
  }
  .categories__poster {
    padding: 49px 24px 77px 24px;
  }
}

@media (max-width: 480px) {
  .header__nav,
  .header__contact {
    display: none;
  }
  .logo:not(span) {
    padding: 16px 0;
  }
  .header__item {
    display: block;
    padding: 16px 0;
  }
  .logo {
    line-height: 28px;
    color: $accent;
    font-size: 24px;
    letter-spacing: 0.03em;
  }
  .logo__color_black {
    color: #000;
  }
  .logo__color_white {
    color: #fff;
  }
  .our-team__social {
    display: block;
  }
  .our-team__social-card {
    margin-bottom: 30px;
  }
  .socials {
    justify-content: center;
  }
  .socials__link:not(:last-of-type) {
    margin-right: 10px;
  }
  .company-pluses__inner {
    display: block;
  }
  .company-pluses__item:not(:last-child) {
    margin-bottom: 30px;
  }
  .company-pluses__title {
    text-align: center;
  }
  .what-we-do {
    display: none;
  }
  .our-team {
    padding: 60px 0;
  }
  .banner__title {
    font-size: 26px;
    line-height: 42px;
    max-width: 360px;
  }
  .banner__order-btn {
      padding: 10px 32px;
  }
  .clients {
    padding: 60px 0;
  }
  .clients__inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
  }
  .footer {
    padding: 60px 0;
  }
  .footer__contacts {
    text-align: center;
    flex-grow: 1;
    margin-bottom: 60px;
  }
  .footer__socials-title {
    text-align: center;
  }
  .footer__socials {
    margin: 0 auto;
  }
  .footer__inner {
    flex-wrap: wrap;
    justify-content: center;
  }
  .footer__mailing {
    margin-top: 60px;
  }
  .footer__mailing-title {
    text-align: center;
  }
  .footer__mailing-input {
    display: block;
    width: 100%;
    max-width: initial;
    margin-bottom: 20px;
    margin-right: initial;
  }
  .footer__mailing-btn {
    margin: 0 auto;
  }
  .footer__form {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }
  .modal {
    overflow: auto;
    max-width: 450px;
    max-height: 609px;
  }
  .modal__agree-mailing,
  .modal__agree-contract {
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.03em;
  }
  .modal__agree-wrapper {
    text-align: initial;
  }
  .categories {
    padding: 60px 0;
  }
  .categories__navigation {
    margin-bottom: 40px;
  }
  .categories__list-item:not(:first-child) {
    margin-bottom: 15px;
  }
  .categories__navigation-list {
    justify-content: start;
    flex-wrap: wrap;
  }
  .categories__poster {
    padding: 49px 24px;
  }
  .categories__inner {
    grid-template-columns: repeat(1, 1fr);
  }
}