.modal {
  max-width: 528px;
  width: 100%;
  max-height: 581px;
  background: #fff;
  position: fixed;
  opacity: 0;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  padding: 40px;
  visibility: hidden;
  border-radius: 4px;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
.modal__general-styles {
  max-width: 448px;
  width: 100%;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
}
.modal__input-general {
  transition: all 0.3s ease;
  padding: 11px 0 11px 42px;
  margin-bottom: 10px;
}
.modal__close-btn {
  border: 1px solid rgba(0, 0, 0, 0.1);
  background: none;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  padding: 6px;
}
.modal__close-btn path {
  transition: all 0.3s ease;
}
.modal__close-btn:hover path {
  fill: #2196F3;
}
.modal__close-wrapper {
  position: absolute;
  width: 18px;
  height: 18px;
  top: 8px;
  right: 14px;
}
.modal__title {
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  letter-spacing: 0.03em;
  color: #212121;
  margin-bottom: 12px;
}
.modal__label {
  display: block;
  position: relative;
}
.modal__label-name {
  display: block;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.01em;
  color: #757575;
  margin-bottom: 4px;
}
.modal__svg {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: translate(16px, 32px);
}
.modal__svg path {
  transition: all 0.3s ease;
}
.modal__comment-area {
  display: block;
  resize: none;
  height: 120px;
  padding: 12px 16px;
  margin-bottom: 20px;
}
.modal__comment-area::placeholder {
  color: #757575;
}
.modal__agree-wrapper {
  display: block;
  text-align: center;
