  position: relative;
  top: 1px;
  width: 16px;
  margin-right: 8px;
}
.modal__agree-mailing {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.03em;
  color: #757575;
}
.modal__agree-contract {
  font-weight: 400;
  font-size: 14px;
