  display: block;
  top: 50%;
  margin-top: -2px;
}

.hamburger-inner,
.hamburger-inner::before,
.hamburger-inner::after {
  width: 24px;
  height: 3px;
  background-color: #212121;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}

.hamburger-inner::before,
.hamburger-inner::after {
  content: "";
  display: block;
}

.hamburger-inner::before {
  top: -7px;
}

.hamburger-inner::after {
  bottom: -7px;
}

.hamburger--squeeze .hamburger-inner {
  transition-duration: 0.075s;
  transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger--squeeze .hamburger-inner::before {
  transition: top 0.075s 0.12s ease, opacity 0.075s ease;
}

.hamburger--squeeze .hamburger-inner::after {
  transition: bottom 0.075s 0.12s ease, transform 0.075s cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.hamburger--squeeze.is-active .hamburger-inner {
  transform: rotate(45deg);
  transition-delay: 0.12s;
  transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
}

.hamburger--squeeze.is-active .hamburger-inner::before {
  top: 0;
  opacity: 1;
  transition: top 0.075s ease, opacity 0.075s 0.12s ease;
}

.hamburger--squeeze.is-active .hamburger-inner::after {
  bottom: 0;
  transform: rotate(-90deg);
  transition: bottom 0.075s ease, transform 0.075s 0.12s cubic-bezier(0.215, 0.61, 0.355, 1);
}

body {
  font-family: "Roboto", sans-serif;
}

button,
input,
textarea {
  font-family: "Roboto", sans-serif;
}

.container {
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
  position: relative;
}

.banner {
  position: relative;
  background: url(../img/main-page/banner.png) no-repeat center/cover;
  text-align: center;
  padding: 200px 0;
}
.banner::before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(47, 48, 58, 0.4);
}
.banner__title {
  display: block;
  font-size: 44px;
  color: #ffffff;
  line-height: 60px;
  font-weight: 900;
  letter-spacing: 0.06em;
  text-align: center;
  text-transform: uppercase;
  max-width: 696px;
  margin: 0 auto;
  margin-bottom: 30px;
}
.banner__order-btn {
  padding: 10px 39px;
}

.company-pluses {
  padding: 94px 0 47px;
}
.company-pluses__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.company-pluses__item {
  flex-basis: calc(25% - 30px);
}
.company-pluses__img-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #F5F4FA;
  margin-bottom: 30px;
}
.company-pluses__img {
  padding: 25px 0;
}
.company-pluses__title {
  display: block;
  font-size: 14px;
  font-weight: bold;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: #212121;
  margin-bottom: 10px;
}
.company-pluses__description {
  font-size: 14px;
  line-height: 24px;
  color: #757575;
  letter-spacing: 0.03em;
}

.what-we-do {
  padding: 47px 0 94px;
}
.what-we-do__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.what-we-do__item {
  position: relative;
  flex-basis: calc(33.3333% - 30px);
}
.what-we-do__image {
  max-width: 100%;
  display: block;
  width: 100%;
}
.what-we-do__image-description {
  position: absolute;
  text-align: center;
  width: 100%;
  font-size: 14px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  font-weight: bold;
  color: #fff;
  padding: 27px 10px;
  bottom: 0;
  left: 0;
  background-color: rgba(44, 50, 53, 0.8);
}

.our-team {
  background-color: #F5F4FA;
  padding: 94px 0;
}
.our-team__social {
  display: flex;
  justify-content: space-between;
}
.our-team__social-card {
  flex-basis: calc(25% - 20px);
  box-shadow: 0px 2px 2px grey;
  text-align: center;
  background: #fff;
}
.our-team__social-photo {
  max-width: 100%;
  width: 100%;
  display: block;
}
.our-team__name {
  display: block;
  font-size: 16px;
  line-height: 18.75px;
  letter-spacing: 0.03em;
  font-weight: 500;
  color: #212121;
  margin-bottom: 10px;
}
.our-team__name-item {
  background: #fff;
  padding: 30px 0 8px;
}
.our-team__profession {
  display: block;
  font-size: 16px;
  font-weight: 400;
  line-height: 18.75px;
  letter-spacing: 0.03em;
  color: #757575;
}
.our-team__socials {
  padding: 8px 32px 30px 32px;
}

.clients {
  padding: 94px 0;
}
.clients__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.clients__item {
  flex-basis: calc(16.666% - 30px);
  border: 1px solid #AFB1B8;
  border-radius: 4px;
}
.clients__item:hover, .clients__item:hover path {
  fill: #2196F3;
  border-color: #2196F3;
}
.clients__item-logo {
  display: flex;
  height: 92px;
}
.clients__item-logo svg {
  margin: auto;
}

.socials {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.socials__link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  border-radius: 50%;
}
.socials__link:hover path, .socials__link:hover {
  fill: #ffffff;
  background: #2196F3;
}
.socials__link path, .socials__link {
  transition: all 0.3s ease;
}

.mobile-menu {
  position: absolute;
  z-index: 1000;
  top: 0;
  right: 0;
  width: 100%;
}
.mobile-menu__inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background-color: #fff;
  max-width: 480px;
  padding: 48px 84px 48px 40px;
  transform: translateX(100%);
  transition: all 0.3s ease;
}
.mobile-menu__button {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1001;
}
.mobile-menu__socials-wrapper {
  display: flex;
}
.mobile-menu__link {
  display: block;
  font-size: 34px;
  line-height: 47px;
  letter-spacing: 0.02em;
  color: #212121;
}
.mobile-menu__link:hover {
  color: #2196F3;
}
.mobile-menu__tel {
  display: block;
  font-size: 28px;
  line-height: 40px;
  letter-spacing: 0.02em;
  color: #2196F3;
}
.mobile-menu__tel:hover {
  color: #1b7ac7;
}
.mobile-menu__email {
  display: block;
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 0.02em;
  color: #757575;
  margin-bottom: 64px;
}
.mobile-menu__email:hover {
  color: #2196F3;
}
.mobile-menu__social-link {
  display: block;
  line-height: 22px;
  letter-spacing: 0.02em;
  color: #2196F3;
  margin: 0 10px;
}
.mobile-menu__social-link:hover {
  color: #1b7ac7;
}
.mobile-menu__social:not(:last-child) {
  border-right: 1px solid rgba(33, 33, 33, 0.2);
}

.ml-0 {
  margin-left: 0;
}

.mr-0 {
  margin-right: 0;
}

.mb-32 {
  margin-bottom: 32px;
}

.general-btn-style {
  font-size: 16px;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 0.06em;
  background: #2196F3;
  color: #ffffff;
  border-radius: 4px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.general-btn-style:hover {
  background: #1b7ac7;
}

.section__title {
  font-weight: 700;
  font-size: 36px;
  line-height: 42.19px;
  text-align: center;
  letter-spacing: 0.03em;
  color: #212121;
  margin-bottom: 50px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s ease;
  z-index: 999;
}

.active-modal {
  opacity: 1;
  pointer-events: all;
  z-index: 99999;
  visibility: visible;
  transition: all 0.3s ease;
}

.active-mobile {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
  transition: all 0.3s ease;
}

.no-scroll {
  overflow: hidden !important;
}

@media (min-width: 481px) {
  .mobile-menu {
    display: none;
  }
}
@media (min-width: 769px) {
  .container {
    max-width: 753px;
  }
}
@media (min-width: 1022px) {
  .container {
    max-width: 1000px;
  }
}
@media (min-width: 1230px) {
  .container {
    max-width: 1200px;
  }
}
@media (max-width: 768px) {
  .logo {
    display: initial;
    font-size: 24px;
    line-height: 28px;
    letter-spacing: 0.03em;
  }

  .header__contact {
    display: block;
  }

  .header__contact-email,
.header__contact-tel {
    font-size: 12px;
    line-height: 14px;
    padding: 0;
  }

  .header__contact-email {
    margin-bottom: 10px;
  }

  .header__nav-link {
    display: inline-block;
  }

  .header__nav {
    margin-left: 86px;
    margin-right: 84px;
  }

  .banner {
    padding: 100px 0;
  }

  .banner__title {
    max-width: 360px;
    font-size: 26px;
    line-height: 42px;
    letter-spacing: 0.06em;
  }

  .banner__order-btn {
    padding: 10px 32px;
  }

  .company-pluses {
    padding: 60px 0;
  }

  .company-pluses__inner {
    flex-wrap: wrap;
    align-items: stretch;
  }

  .company-pluses__item {
    flex-basis: calc(50% - 30px);
  }
  .company-pluses__item:nth-child(-n+2) {
    margin-bottom: 30px;
  }

  .what-we-do {
    display: none;
  }

  .section__title {
    font-size: 28px;
    line-height: 33px;
  }

  .our-team {
    padding: 60px 0;
  }

  .our-team__social {
    flex-wrap: wrap;
  }

  .our-team__social-card {
    flex-basis: calc(50% - 15px);
  }
  .our-team__social-card:nth-child(-n+2) {
    margin-bottom: 30px;
  }

  .socials {
    justify-content: center;
  }

  .socials__link:not(:last-of-type) {
    margin-right: 10px;
  }

  .clients {
    padding: 60px 0;
  }

  .clients__inner {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
  }

  .footer__inner {
    flex-wrap: wrap;
    justify-content: center;
  }

  .footer__contacts {
    text-align: center;
    margin-bottom: 60px;
  }

  .footer__socials {
    text-align: center;
    margin-left: 165px;
    margin-right: 0;
  }

  .footer__mailing {
    text-align: center;
    margin: auto;
  }

  .footer__mailing-input {
    max-width: 450px;
    margin-bottom: 20px;
    margin-right: 0;
  }

  .footer__mailing-btn {
    display: block;
    padding: 10px 28px;
    margin: 0 auto;
  }

  .categories {
    padding: 60px 0;
  }

  .categories__navigation {
    margin-bottom: 30px;
  }

  .categories__inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  .categories__poster {
    padding: 49px 24px 77px 24px;
  }
}
