  }

  .our-team__social {
    display: block;
  }

  .our-team__social-card {
    margin-bottom: 30px;
  }

  .socials {
    justify-content: center;
  }

  .socials__link:not(:last-of-type) {
    margin-right: 10px;
  }

  .company-pluses__inner {
    display: block;
  }

  .company-pluses__item:not(:last-child) {
    margin-bottom: 30px;
  }

  .company-pluses__title {
    text-align: center;
  }

  .what-we-do {
    display: none;
  }

  .our-team {
    padding: 60px 0;
  }

  .banner__title {
    font-size: 26px;
    line-height: 42px;
    max-width: 360px;
  }

  .banner__order-btn {
    padding: 10px 32px;
  }

  .clients {
    padding: 60px 0;
  }

  .clients__inner {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 30px;
    grid-row-gap: 30px;
  }

